export default{
  data(){
    return{
      isApply:true
    }
  },
  watch: {
    // 如果路由有变化，会再次执行该方法
    '$route': 'isApplyFn'
  },
  methods:{
    isApplyFn(){
      var b_isApply = /\/apply\//.test(this.$route.path);
      this.isApply=b_isApply;
      // console.log(b_isApply, this.isApply=b_isApply ,'路由变化')
    }
  }
}



